import { compose, withHandlers, mapProps } from 'recompose'
import { withGetStructure } from './queries'

export default compose(
  // Here's the query that gives us structure
  withGetStructure,
  // ONCE we mount the control we should initialize it
  withHandlers({
    getValueProps: ({ structure }) => (name) => {
      try {
        return structure.body[name]
      }
      catch (e) {}
    },
    setValue: ({ structure }) => (name, newVal) => {
      console.log('setValue called', { structure, name, newVal })
      // dispatch(actions.changeFieldValue(formGuid, name, newVal))
    },
    setNote: ({ structure }) => (name, newNote) => {
      console.log('setNote called', { structure, name, newNote })
      // dispatch(actions.changeFieldValue(formGuid, name, null, newNote))
    }
  })
)

